<template>
    <div>
        <div class="power-content">
            <div class="circle-wrapper">
                <span class="number">{{val}}%</span>
                <circle-slider progress-color="#168efc" :side="150" v-model="val" :knob-radius-rel="100"></circle-slider>
            </div>
            <div class="slider-wrapper">
                <vue-slider v-model="val" @dragging="dragging">
                    <template v-slot:dot>
                        <img src="../../assets/img/wheel-power-icon.png" class="custom-dot" />
                    </template>
                </vue-slider>
            </div>
        </div>
    </div>
</template>

<script>
    import VueSlider from 'vue-slider-component'
    import 'vue-slider-component/theme/default.css'
    import {
        mapState,
        mapGetters,
        mapActions,
        mapMutations
    } from 'vuex'
    export default {
        name: 'ControllPower',
        components: {
            VueSlider
        },
        data() {
            return {
                val: 50,
                value: 0,
                "SP": {'SP': 50}
            }
        },
        computed: {
            ...mapGetters(['msgObj', 'line_', 'cliff_']),
        },
        methods: {
            ...mapActions(['send']),
            dragging (value) {
                this.$set(this['SP'], 'SP', value)
                this.send(JSON.stringify(this['SP']));
            }
        }
    }
</script>

