<template>
    <div>
        <div class="content">
            <div class="switch-bar">
                <div class="switch-text">
                    <span>Crawler</span>
                    <div class="value-content">
                        <div class="value-block">
                            X:
                            <span class="value-number">{{legValue[0].toFixed(2)}}</span>
                            mm
                        </div>
                        |
                        <div class="value-block">
                            Y:
                            <span class="value-number">{{legValue[1].toFixed(2)}}</span>
                            mm
                        </div>
                        |
                        <div class="value-block">
                            Z:
                            <span class="value-number">{{legValue[2].toFixed(2)}}</span>
                            mm
                        </div>
                    </div>
                </div>
                <div class="servo-img-ok" @click="handleSetOkClick">
                    <img class="img-ok" src="../../assets/img/OK.png" alt="">
                </div>
            </div>
            <div class="area">
                <div class="img-content">
                    <div @click="handleBaseClick(index, item.leg)" ref="baseImgContent"  :class="item.class" v-for="(item, index) of baseList" :key="item.id">
                        <img ref="baseImg" class="baseImg" src="../../assets/img/base.png" alt="">
                    </div>
                    <!-- <div class="leftBottom">
                        <img class="baseImg" src="../../assets/img/base.png" alt="">
                    </div>
                    <div class="rightTop">
                        <img class="baseImg" src="../../assets/img/base.png" alt="">
                    </div>
                    <div class="rightBottom">
                        <img class="baseImg" src="../../assets/img/base.png" alt="">
                    </div> -->
                    <img class="crawler-img" src="../../assets/img/crawler-model.png" alt="">
                    <div class="controll-content">
                        <div class="xBtn">
                            <SettingDirection 
                                :cate="cate" 
                                :data="dataX"  
                                :rotate="rotateY" 
                                :side="widthX"
                                :dire="direX"
                                :revDire="revDireX"
                                :num="num"
                            >
                            </SettingDirection>
                        </div>
                         <div class="yBtn">
                             <SettingDirection 
                                :rotate="rotateX" 
                                :side="widthX"
                                :data="dataX"
                                :dire="direY"
                                :revDire="revDireY"
                                :num="num"
                                :cate="cate"
                             >
                             </SettingDirection>
                        </div>
                         <div class="zBtn">
                             <SettingDirection
                                :rotate="rotateZ" 
                                :side="widthX"
                                :data="dataX"
                                :dire="direZ"
                                :revDire="revDireZ"
                                :num="num"
                                :cate="cate"
                            >
                            </SettingDirection>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import SettingDirection from '@/components/common/Direction'
    import { mapGetters, mapActions } from 'vuex'
    export default {
        name: 'SettingCrawler',
        components: {
            SettingDirection,
        },
        data() {
            return {
                cate: 'OF',
                rotateX: {
                    transform: 'rotate(0deg)'
                },
                rotateY: {
                    transform: 'rotate(90deg)'
                },
                rotateZ: {
                    transform: 'rotate(0deg)'
                },
                widthX: 35,
                dataX: {
                    'OF': ['on', 1, 'up', 0]
                },
                reverDataX: {
                    'OF': ['on', 1, 'down', 0]
                },
                num: 1,
                direX: 'right',
                revDireX: 'left',
                direY: 'up',
                revDireY: 'down',
                direZ: 'high',
                revDireZ: 'low',
                baseList: [{
                    id: '0001',
                    class: 'leftTop',
                    leg: 2
                }, {
                    id: '0002',
                    class: 'leftBottom',
                    leg: 3
                }, {
                    id: '0003',
                    class: 'rightTop',
                    leg: 1
                }, {
                    id: '0004',
                    class: 'rightBottom',
                    leg: 4
                }]
            }
        },
        computed: {
            ...mapGetters(['msgObj']),
            legValue () {
                if (this.msgObj) {
                    return this.msgObj['AS']
                }else {
                    return [0, 0, 0]
                }
            }
        },
        methods: {
            ...mapActions(['send']),
            handleBaseClick (index, leg) {
                this.isBaseShow(index);
                console.log(leg);
                this.num = leg
                let data = {
                    "OF": ['off', leg, 'up', 0]
                }
                this.send(JSON.stringify(data))
            },
            isBaseShow (index) {
                // console.log(index)
                // this.$refs.baseImg.style.visibility = 'hidden';
                const el = this.$refs.baseImg;
                const pEl = this.$refs.baseImgContent;
                el.forEach((item, key) => {
                    item.style.visibility = 'hidden'
                })
                pEl.forEach((item, key) => {
                    item.style.zIndex = 2;
                })
                this.$refs.baseImg[index].style.visibility = 'visible';
                this.$refs.baseImgContent[index].style.zIndex = 0;
            },
            defaultBaseVisible () {
                this.$refs.baseImg[2].style.visibility = 'visible';
                this.$refs.baseImgContent[2].style.zIndex = 0;
            },
            handleSetOkClick () {
                let data = {
                    "OF": ['on', this.num, 'a', 1]
                }
                this.send(JSON.stringify(data));
            }
        },
        mounted () {
            this.defaultBaseVisible ();
        }
    }
</script>

