<template>
    <div>
        <div class="content" :style="this.rotate">
            <div 
                @touchstart="handleDireClick(cate,data)"
                @touchend="handleEnd(cate, data)" 
                class="img-content"
            >
                <img :style="{'width': side + '%'}" class="direction-img marginBottom" src="../../assets/img/arrow.png"
                    alt="">
            </div>
            <div class="img-content"
                @touchstart="handleReDireClick(cate,reverData)"
                @touchend="handleEnd(cate, data)"   
            >
                <img  :style="{'width': side + '%'}"
                    class="direction-img rotate" src="../../assets/img/arrow.png" alt="">
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapMutations,
        mapActions,
        mapGetters
    } from 'vuex'
    export default {
        name: 'Direction',
        props: {
            rotate: {
                type: Object,
                default: function () {
                    return {
                        transfrom: 'rotate(0deg)'
                    }
                }
            },
            side: {
                type: Number,
                default: function () {
                    return 70;
                }
            },
            data: {
                type: Object,
                default: () => {
                    return {
                        'RC': 'forward'
                    }
                }
            },
            reverData: {
                type: Object,
                default: () => {
                    return {
                        "RC": 'forward'
                    }
                }
            },
            cate: {
                type: String,
            },
            dire: {
                type: String,
            },
            revDire: {
                type: String,
            },
            num: {
                type: Number
            }
        },
        data() {
            return {
                res: this.data,
                reverRes: this.reverData,
                resNum: this.num,
                last: {"RC": 'null'},
                resDire: this.dire,
                resrevDire: this.revDire
            }
        },
        methods: {
            ...mapActions(['send']),
            handleDireClick(cate, data, d, rd) {
                if (cate == 'OF') {
                    let data = {
                        "OF": ['on', this.num, this.resDire, 0]
                    }
                    this.send(JSON.stringify(data));
                }else {
                    this.send(JSON.stringify(this.res))
                }
                // console.log(this.res);
            },
            handleReDireClick(cate,data, d, rd) {
                if (cate == 'OF') {
                    let data = {
                        "OF": ['on', this.num, this.resrevDire, 0]
                    }
                    this.send(JSON.stringify(data));
                }else {
                    this.send(JSON.stringify(this.reverRes))
                }
                // console.log(data)
            },
            handleEnd (cate, data) {
                if (cate == 'OF') {
                    let data = {
                        "OF": ['off', this.num, this.resrevDire, 0]
                    }
                    this.send(JSON.stringify(data));
                    return
                }
                this.send(JSON.stringify(this.last))
            }
        }
    }
</script>

<style scoped>
    .content {
        /* background: rgba(255, 255, 255, 0.3);
        border-radius: 50%; */
    }

    .img-content {
        text-align: center;
    }

    .direction-img {
        width: 70%;
    }

    .rotate {
        transform: rotate(180deg);
    }

    .marginBottom {
        margin-bottom: 0.2rem;
    }
</style>